import React from "react";
import "./Video.scss";
import Heading from "../../components/Heading/Heading";
import Navbar from "../../components/Navbar/Navbar";
import movie from "../../assest/movie.mp4";
import { DefaultPlayer as Video } from "react-html5video";
import "react-html5video/dist/styles.css";
const Videos = () => {
  return (
    <>
      <div className="video-page">
        <div className="video-page-topbar">
          <Navbar />
        </div>

        <div className="video-page-container">
          <div className="video-page-container-heading">
            <center>
              <Heading text="Featured" />
            </center>
          </div>
          <div className=""></div>
        </div>
        <div className="video-page-content">
          <center>
            <Heading text="More from this week" />
          </center>
          <di className="video-page-content-videos">
            <Video
            
              autoPlay
              loop
              muted
              controls={["PlayPause", "Seek", "Time", "Volume", "Fullscreen"]}
            >
              <source src={movie}></source>
            </Video>
          </di>
        </div>
      </div>
    </>
  );
};

export default Videos;
